@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);

body, html {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#timeStringHeader {
  color: white;
  font-size: 5vh;
  text-align: center;
  
}

#timeStringHeaderWrap {
  position: static;
  bottom: 10vh;
  padding: 10px 30px;
  border-radius: 5px;
  width: 300px;
}
#introMent {
  color: white;
  font-size: 8vh;

  border-right: 4px solid grey;
  -webkit-animation-name: cursor;
          animation-name: cursor;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@media (max-width: 562px) {
  #introMent {
    font-size: 4vh;
  }
}

@-webkit-keyframes cursor {
  from {
    border-right: 4px solid rgba(200,200,200, 0.0);
  }

  to {
    border-right: 4px solid rgba(200,200,200, 1.0);
  }
}

@keyframes cursor {
  from {
    border-right: 4px solid rgba(200,200,200, 0.0);
  }

  to {
    border-right: 4px solid rgba(200,200,200, 1.0);
  }
}
.intro-wrap {
  width: 100vw;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7)) ), url('/img/intro_background.jpg');
  background: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url('/img/intro_background.jpg');
  background-attachment: fixed;
  background-size: cover;
  flex-flow: column;
}

@media (max-width: 562px) {
  .intro-wrap {
    height: 30vh;
  }
}
#nav-wrap {
  width: 60vw;
}
 
.navbar {
  background: rgba(0,0,0,0.5);
}
.about-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20vh;
  width: 100%;
}

.skills-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20vh;
  width: 100%;
}
.common-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5vh;
  width: 100%;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80vw;
}

@media (max-width: 712px) {
  .row {
    flex-direction: column;
  }
}

.col-lg{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  word-wrap: none;
  height: 300px;
  max-height: 500px;
  font-weight: 500;
}

.media {
  height: 300px;
}
.col > p {
  text-align: justify;
  overflow: scroll;
}

p {
  font-size: 20px;
}

.footer {
  width: 100vw;
  margin-top: 5vh;
  padding: 5vh;
  background: #222222;
}

#copyright {
  color: grey;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Courier New', Courier, monospace;
}
